import React from "react";
import { Outlet } from "react-router-dom";

import appRoutes from "constants/appRoutes";
import Tools from "components/Tools";
import { getLastPart } from "helpers/string";
import worldClockIcon from "images/world-clock.svg";

export default function Time() {
  return (
    <>
      <Tools listMap={toolsListMap} />
      {/* Outlet for child routes */}
      <Outlet />
    </>
  );
}

const toolsListMap = new Map([
  [
    appRoutes.toolsTimeWorldClock.path,
    {
      name: getLastPart(appRoutes.toolsTimeWorldClock.path, "/"),
      icon: worldClockIcon,
      description: appRoutes.toolsTimeWorldClock.title
    }
  ]
]);
